.question {
  background: url("/img/form-background.jpg") no-repeat, $color-deep-sky-blue;
  margin: 0 auto;
  max-width: 1920px;
  object-fit: cover;
  background-position: center top;

  @include vp-1023 {
    background: $color-deep-sky-blue;
  }

  @media (-webkit-min-device-pixel-ratio: 2),
  (min-resolution: 2dppx) {
    background: url("/img/form-background@2x.jpg") no-repeat, $color-deep-sky-blue;
  }
}

.question__container {
  display: flex;
  padding: 166px 15px 225px 5px;
  margin: 0 auto;
  max-width: 1210px;
  justify-content: space-between;
  box-sizing: content-box;

  @include vp-1023 {
    padding: 50px 10px 125px 10px;
    max-width: 800px;
    justify-content: center;
  }

  @include vp-767 {
    max-width: 500px;
    padding: 50px 25px 90px 25px;
  }
}

.question__image {
  padding-top: 80px;
  min-width: 300px;
  margin-right: 10px;
  padding-left: 0px;

  @include vp-1023 {
    display: none;
  }
}

.question__form {
  max-width: 585px;
  padding-bottom: 15px;
}
